// ハンバーガーメニュー・著作権色変更
.is-white{
  .humburger{
    border-color: #3e3a39;
    &::after{
      color: #3e3a39;
    }
    &__line{
      background: #3e3a39;
      &::before,&::after{
        background: #3e3a39;
      }
    }
  }
  .footer{
    &__copy{
      color: #3e3a39;
    }
  }
}

.is-active {
  .humburger {
    border-color: #ffffff;
    color: #ffffff;
    &::after {
      color: #ffffff;
      content: "CLOSE";
    }
    &__line {
      background: transparent;
      &::before {
        background: #ffffff;
        top: 0;
        transform: rotate(405deg);
      }
      &::after {
        background: #ffffff;
        top: 0;
        transform: rotate(-45deg);
      }
    }
  }
  .sp-menu {
    opacity: 1;
    pointer-events: initial;
  }
  .sp-menu-bg {
    opacity: 1;
    pointer-events: initial;
  }
}

.humburger {
  position: fixed;
  z-index: 200;
  top: 4rem;
  right: 5vw;
  display: inline-block;
  width: clamp(5rem, 4.375vw, 7rem);
  height: clamp(5rem, 4.375vw, 7rem);
  background: none;
  border: 0.2rem solid #ffffff;
  border-radius: 50%;
  transition: 0.3s ease;
  &:focus,
  &:hover {
    box-shadow: 0 0 0.5rem rgba(161, 161, 161, 0.5);
  }
  &::after {
    bottom: clamp(-3rem, -1.875vw, -2.3rem);
    color: #ffffff;
    content: "MENU";
    display: block;
    font-family: "Playfair Display", serif;
    font-size: clamp(1.2rem, 1vw, 1.6rem);
    left: 0;
    letter-spacing: 0.2em;
    margin: auto;
    position: absolute;
    right: 0;
    transition: inherit;
  }
  &__line {
    background: #ffffff;
    bottom: 0;
    display: inline-block;
    height: 2px;
    left: 0;
    margin: auto;
    position: absolute;
    right: 0;
    top: 0;
    transition: 0.3s ease;
    width: clamp(2.5rem, 2vw, 3.2rem);
    &::before,
    &::after {
      background: #ffffff;
      content: "";
      display: inline-block;
      height: 100%;
      left: 0;
      position: absolute;
      top: 0;
      transition: inherit;
      width: 100%;
    }
    &::before {
      top: 1rem;
    }
    &::after {
      top: -1rem;
    }
  }
  @media screen and (min-width: 600px) {
    left: 5vw;
    top: 7rem;
  }
}

.sp-menu {
  position: fixed;
  z-index: 150;
  top: 0;
  left: 0;
  width: 45rem;
  max-width: 100%;
  height: 100%;
  padding: 20rem 6rem 15rem;
  background: #000000;
  // display: none;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s opacity ease;
  @media screen and (min-width: 600px) {
    padding: 25rem 7.5rem 15rem;
  }
  &__list {
    border-bottom: 1px solid #ffffff;
    border-top: 1px solid #ffffff;
    padding: 6rem 0;
  }
  &__item:nth-of-type(n + 2) {
    margin-top: 3.5rem;
  }
  &__item a {
    color: #ffffff;
    font-family: "Playfair Display", serif;
    font-size: 2rem;
    letter-spacing: 0.25em;
    text-transform: uppercase;
    transition: 0.3s ease;
    &:focus,
    &:hover {
      opacity: 0.5;
    }
  }
  &__item--active {
    opacity: 0.5;
    pointer-events: none;
  }
}

.sp-menu-bg {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  overflow: hidden;
  // display: none;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s opacity ease;
}
