@use "../../base/base" as b;
@use "../../base/mixin" as m;

@keyframes scroll-bar {
  0% {
    opacity: 0;
    transform: scaleY(0);
    transform-origin: top;
  }
  5% {
    opacity: 1;
    transform: scaleY(0);
  }
  50% {
    opacity: 1;
    transform: scaleY(100%);
  }
  95% {
    opacity: 1;
    transform: scaleY(0);
    transform-origin: bottom;
  }
  100% {
    opacity: 0;
    transform: scaleY(0);
    transform-origin: bottom;
  }
}

.mv {
  height: 100vh;
  position: relative;
  z-index: 0;
  &::after {
    background-color: rgba(0, 0, 0, 0.26);
    content: "";
    display: block;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 5;
  }
  &__inner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 15;
  }
  &__content {
    width: 100%;
    max-width: 108rem;
    margin-left: auto;
    margin-right: auto;
  }
  &__title {
    color: #ffffff;
    font-family: "Playfair Display", serif;
    font-size: clamp(4.5rem, 4.0625vw, 8.5rem);
    font-weight: bold;
    letter-spacing: 0.2em;
  }
  &__catch {
    color: #ffffff;
    font-size: clamp(1.6rem, 1.25vw, 4rem);
    margin-top: 1.5rem;
    text-align: center;
    @media screen and (min-width: 600px) {
      margin-top: 2rem;
    }
  }
  &__scroll {
    display: block;
    -ms-writing-mode: tb-lr;
    bottom: 0;
    color: #ffffff;
    font-size: 1.4rem;
    height: 11rem;
    letter-spacing: 0.3em;
    line-height: 2.5;
    padding: 1.5em 0;
    position: absolute;
    right: 3vw;
    width: 3rem;
    writing-mode: vertical-lr;
    z-index: 20;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 1px;
      background: #ffffff;
      @include m.animation(
        $name: scroll-bar,
        $duration: 2.5s,
        $timing-function: cubic-bezier(1, 0, 0, 1),
        $iteration-count: infinite,
        $fill-mode: none
      );
    }
  }
  @media screen and (min-width: 600px) {
    &__scroll {
      height: 14rem;
    }
  }
}

// Swiper設定
@keyframes zoom-up {
  /* 1.15倍させる指定 */
  0% {
    transform: scale(1.25);
  }
  100% {
    transform: scale(1);
  }
}
.swiper-mv {
  height: 100%;
  position: relative;
  z-index: 0;
  .swiper-slide {
    overflow: hidden;
    backface-visibility: hidden;
  }
  .swiper-slide-active .slide-img,
  .swiper-slide-duplicate-active .slide-img,
  .swiper-slide-prev .slide-img {
    /* 12秒かけて拡大させる */
    animation: zoom-up 12s linear 0s;
    animation-fill-mode: both;
  }
  .slide-img {
    background-size: cover;
    background-position: center center; /* 背景画像は中央を軸に表示させる */
    height: 100%;
    width: 100%;
  }
  .one {
    background: url(../img/layout/top/flower-blue2.webp) no-repeat center
      center/cover;
  }
  .two {
    background: url(../img/layout/top/top-mv4.webp) no-repeat center
      center/cover;
  }
  .three {
    background: url(../img/layout/top/top-mv2.webp) no-repeat center
      center/cover;
  }
  .four {
    background: url(../img/layout/top/top-mv3.webp) no-repeat center
      center/cover;
  }
}
