@use '../base/base' as b;

.footer__copy {
  display: none;
  @media screen and (min-width: 600px) {
    bottom: 0;
    color: #ffffff;
    display: block;
    font-family: "Playfair Display", serif;
    font-size: clamp(1.2rem, 0.875vw, 1.4rem);
    height: 25px;
    left: 0;
    letter-spacing: 2px;
    margin: auto;
    max-width: 100%;
    position: fixed;
    text-align: center;
    top: 0;
    transform: translateX(-140px) rotate(-90deg);
    transition: 0.3s ease;
    width: 350px;
    z-index: 3;
  }
}